.HobbiesSection {
    .hobbies {
        width: 95%;
        background-color: #191d2b;
        border-left: 1px solid #2e344e;
        border-right: 1px solid #2e344e;
        border-bottom: 1px solid #2e344e;
        border-top: 8px solid #2e344e;
        transition: all 0.4s ease-in-out;
        @media screen and (max-width: 1400px) {
            flex-direction: column;
            margin-bottom: 1rem;
        }
        &:hover {
            border-top: 8px solid #037fff;
        }

        .hobbies-content {
            padding: 1rem;
            .h-title {
                font-size: 1.6rem;
                font-weight: 500;
                position: relative;
                padding-bottom: 1rem;
                margin: 1rem 0;
                &::after {
                    content: "";
                    position: absolute;
                    bottom: 0;
                    left: 0;
                    width: 30%;
                    height: 1px;
                    background-color: #2e344e;
                }
            }
        }
    }
}

.hobbies-container {
    display: flex;
    justify-content: space-between;
    margin-top: 4rem;
    @media screen and (max-width: 1400px) {
        flex-direction: column;
        &:not(:last-child) {
            margin-bottom: 2rem;
        }
    }
}
